<template>        
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3><b>分析結果</b></h3>
    </div>
    <br>
    <div class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <div class="card-body">
            <h4>您沒有違反告知義務，仍得請求理賠</h4>
            <br>
            <div>
                <svg xmlns="http://www.w3.org/2000/svg" width="80" height="80" fill="green" class="bi bi-check-circle" viewBox="0 0 16 16">
                    <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                    <path d="M10.97 4.97a.235.235 0 0 0-.02.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-1.071-1.05z"/>
                </svg>
            </div>
            <br>
            <h5 class="text-danger">在保險業、保險經紀人公司、保險代理人公司或兼營保險代理人或保險經紀人業務之銀行，從事保險招攬之人，具有告知受領權限</h5>
        </div>
        <br>
        <div v-if="getAPI === false">
            <h5>正在為您搜尋與您情況相似的判決書....</h5>
            <br>
            <div class="d-flex justify-content-center">
                <div class="spinner-border text-secondary" style="width: 3rem; height: 3rem;" role="status">
                    <span class="sr-only"></span>
                </div>
            </div>
        </div>
        <div v-if="getAPI === true" class="card">
            <div class="card-body">
                <h5>以下為推薦給您參考的判決書....</h5>
                <a v-for="(item, index) in filenames" :key="index" :href="'https://www.lawsnote.com/search/all/'+item" target="_blank" class="list-group-item list-group-item-action list-group-item-light">{{item}}</a>
            </div>
        </div>
        <br>
        <div>
            <button @click="press" class="btn btn-outline-dark" style="padding: 10px 30px width: 70%;">
                <b>回首頁</b>
            </button>
        </div>
    </div>
</template>

<script>
import axios from "axios";
axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded';

export default {
    data() {
        var query = this.$route.params.query;
        var filenames = ['100,投保險簡,2', '100,港保險,1', '106,保險,20', '106,保險,3', '100,保險,21']; // this.api(query);

        return{
            time: 0,
            getAPI: false,
            filenames,
            query
        }
    },
    methods: {
        api (query){
            var data = null;
            axios.get('http://127.0.0.1:5000/filenames', {
            headers: {}, params: { query: query }
            }).then(res => {
                data = res.data;
                console.log(data['filenames']);
                this.filenames = data['filenames'];
                this.getAPI = true;
                console.log(res);
            }).catch(err => {
                console.log(err.response);
            });
        },
        press: function(){
            this.$router.push({name: 'Home'})
        },
        countDown (){
            let _this = this;
            _this.time--;
        }
    },
    mounted() {
        let _this = this;
        _this.time = 3;
        setInterval(_this.countDown, 1000);
    },
    watch: {
        time: function(newVal){
            if(newVal == 0){
                this.getAPI = true;
            }
        }
    }
};
</script>